<script>
import backgroundImage from "@/assets/DailyOperation/background/1-3.jpg";
import LineChartLimit from "@/components/LineChartLimit";
import websockObject from "@/mixins/websock";
const tickcount = 20;
export default {
  name: "CapacityLineChart",
  mixins: [websockObject],
  data() {
    return {
      fristBack: true,
      // eslint-disable-next-line no-undef
      orderObject: dailyLeftBottom,
      backgroundImage,
      CapacityLineHeader: [],
      CapacityLineLeft: [],
      CapacityLineRight: []
    };
  },
  components: {
    LineChartLimit
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.dailyLeftBottom.websockUrl;
    // this.useTheTestDate()
  },
  methods: {
    useTheTestDate() {
      let tillTiem = new Date().getTime();
      let m = 10000;
      let n = 0;
      let test = [
        {
          dateTime: tillTiem,
          limit: 0,
          type: "top",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "行情、报盘1",
          group: "行情、报盘"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "top",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "行情、报盘2",
          group: "行情、报盘"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "top",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "行情、报盘3",
          group: "行情、报盘"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "top",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "行情、报盘4",
          group: "行情、报盘"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "top",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "数据库3",
          group: "数据库"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "top",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "数据库4",
          group: "数据库"
        },

        // {'dateTime': tillTiem, limit: 0, type: 'middle', 'value': Math.floor(Math.random() * (m - n) + n), 'indicateName': 'nginx接入1', 'group': 'nginx接入'},
        // {'dateTime': tillTiem, limit: 0, type: 'middle', 'value': Math.floor(Math.random() * (m - n) + n), 'indicateName': 'nginx接入2', 'group': 'nginx接入'},
        // {'dateTime': tillTiem, limit: 0, type: 'middle', 'value': Math.floor(Math.random() * (m - n) + n), 'indicateName': '消息总线1', 'group': '消息总线'},
        // {'dateTime': tillTiem, limit: 0, type: 'middle', 'value': Math.floor(Math.random() * (m - n) + n), 'indicateName': '消息总线2', 'group': '消息总线'},

        {
          dateTime: tillTiem,
          limit: 0,
          type: "middle",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "中台服务器1",
          group: "中台服务器"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "middle",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "中台服务器2",
          group: "中台服务器"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "middle",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "行情、报盘1",
          group: "行情、报盘"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "middle",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "行情、报盘2",
          group: "行情、报盘"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "middle",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "数据库1",
          group: "数据库"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "middle",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "数据库2",
          group: "数据库"
        },

        {
          dateTime: tillTiem,
          limit: 0,
          type: "bottom",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "数据库1",
          group: "数据库"
        },
        {
          dateTime: tillTiem,
          limit: 0,
          type: "bottom",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "数据库2",
          group: "数据库"
        },
        // {'dateTime': tillTiem, limit: 0, type: 'bottom', 'value': Math.floor(Math.random() * (m - n) + n), 'indicateName': '交易、行情转换机', 'group': '交易、行情转换机'},
        {
          dateTime: tillTiem,
          limit: 0,
          type: "bottom",
          value: Math.floor(Math.random() * (m - n) + n),
          indicateName: "as",
          group: "as"
        }
        // {'dateTime': tillTiem, limit: 0, type: 'bottom', 'value': Math.floor(Math.random() * (m - n) + n), 'indicateName': '恒生O32中间件ar', 'group': '恒生O32中间件ar'}
      ];
      this.getWebsocketMsg(test);
      let _this = this;
      this.timeOut && clearTimeout(this.timeOut);
      this.timeOut = setTimeout(() => {
        _this.useTheTestDate();
      }, 2000);
    },
    getWebsocketMsg(sourDate) {
      let self = this;
      if (sourDate.length > 0) {
        let historyDate = [];
        sourDate.forEach(element => {
          element.group = element.group
            .replace(/\s+/g, "")
            .replace("自营_", "")
            .replace("量化家_", "")
            .toUpperCase();
          if (element.type === "top") {
            historyDate = self.CapacityLineHeader;
          }
          if (element.type === "middle") {
            historyDate = self.CapacityLineLeft;
          }
          if (element.type === "bottom") {
            historyDate = self.CapacityLineRight;
          }
          let target = historyDate.findIndex(item => {
            return item.group === element.group;
          });
          if (target === -1) {
            let item = {
              indicateList: [
                {
                  indicateName: element.indicateName,
                  value: []
                }
              ],
              group: element.group
            };
            if (element.type === "top") {
              item.indicateList[0].value = element.value;
              self.CapacityLineHeader.push(item);
            } else {
              item.indicateList[0].value.push(element);
              if (element.type === "middle") {
                self.CapacityLineLeft.push(item);
              }
              if (element.type === "bottom") {
                self.CapacityLineRight.push(item);
              }
            }
          } else {
            let item = historyDate[target];
            let isIndicateNameSet = item.indicateList.findIndex(
              indicateitem => {
                return indicateitem.indicateName === element.indicateName;
              }
            );
            if (isIndicateNameSet === -1) {
              let indicate = {
                indicateName: element.indicateName,
                value: []
              };
              if (element.type === "top") {
                indicate.value = element.value;
              } else {
                indicate.value.push(element);
              }
              item.indicateList.push(indicate);
              item.indicateList.sort((a, b) => {
                return (
                  Number(a.indicateName.replace(/[^\d]/g, " ")) -
                  Number(b.indicateName.replace(/[^\d]/g, " "))
                );
              });
            } else {
              let base = item.indicateList[isIndicateNameSet].value;
              if (element.type === "top") {
                item.indicateList[isIndicateNameSet].value = element.value;
              } else {
                base.push(element);
                item.indicateList[isIndicateNameSet].value = base.slice(
                  -1 * tickcount
                );
              }
            }
            if (element.type === "top") {
              self.$set(self.CapacityLineHeader, target, item);
            }
            if (element.type === "middle") {
              self.$set(self.CapacityLineLeft, target, item);
            }
            if (element.type === "bottom") {
              self.$set(self.CapacityLineRight, target, item);
            }
          }
        });
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    }
  },
  render(h) {
    return (
      <section
        class="mainBox"
        {...{
          style: { backgroundImage: " url(" + this.backgroundImage + ")" }
        }}
      >
        <section class="mainChartBox mainChartHeader">
          <div class="title mainTitile">{this.orderObject[0].title}</div>
          <section class="LineChartBox">
            <section class="indicateList">
              {this.CapacityLineHeader.map(item => {
                return item.indicateList.map(indicateItem => {
                  return (
                    <section
                      class="indicateItem"
                      {...{
                        style: {
                          order: this.orderObject[0].order.indexOf(
                            item.indicateName
                          )
                        }
                      }}
                    >
                      <div class="indicateNum mainFont">
                        {indicateItem.value}
                      </div>
                      <div class="indicateNindicateNameum">
                        {indicateItem.indicateName}
                      </div>
                    </section>
                  );
                });
              })}
            </section>
          </section>
        </section>
        <section class="mainChartBox mainChartLeft">
          <section class="LineChartBox">
            <section
              class="contentBox"
              {...{
                style: {
                  width: "1784px",
                  height: "360px",
                  margin: "auto",
                  paddingTop: "20px"
                }
              }}
            >
              {this.CapacityLineLeft.map((item, index) => {
                return (
                  <div
                    class="LineChartItem"
                    key={"CapacityLineLeft" + index + item.indicateList.length}
                    {...{
                      style: {
                        order: this.orderObject[0].order.indexOf(item.group),
                        width: "337px"
                      }
                    }}
                  >
                    {item.indicateList.map(indicateItem => {
                      return (
                        <LineChartLimit
                          sourDate={indicateItem.value}
                          title={indicateItem.indicateName}
                        ></LineChartLimit>
                      );
                    })}
                  </div>
                );
              })}
            </section>
          </section>
        </section>
        <section class="mainChartBox mainChartRight">
          <div class="title mainTitile" {...{ style: { top: "-10px" } }}>
            {this.orderObject[1].title}
          </div>
          <section class="LineChartBox">
            <section
              class="contentBox"
              {...{
                style: {
                  width: "1784px",
                  height: "280px",
                  margin: "auto",
                  paddingTop: "20px"
                }
              }}
            >
              {this.CapacityLineRight.map((item, index) => {
                return (
                  <div
                    class="LineChartItem"
                    key={"CapacityLineRight" + index + item.indicateList.length}
                    {...{
                      style: {
                        order: this.orderObject[1].order.indexOf(item.group),
                        width: "426px"
                      }
                    }}
                  >
                    {item.indicateList.map(indicateItem => {
                      return (
                        <LineChartLimit
                          sourDate={indicateItem.value}
                          title={indicateItem.indicateName}
                        ></LineChartLimit>
                      );
                    })}
                  </div>
                );
              })}
            </section>
          </section>
        </section>
      </section>
    );
  }
};
</script>

<style scoped>
.mainChartBox {
  display: flex;
  flex-flow: column;
}
.mainChartHeader {
  height: 225px;
  width: 1833px;
  position: absolute;
  top: 0;
  left: 60px;
}
.mainChartLeft {
  width: 1833px;
  height: 380px;
  position: absolute;
  top: 230px;
  left: 60px;
}
.mainChartRight {
  width: 1833px;
  height: 380px;
  position: absolute;
  top: 610px;
  left: 60px;
}
.title {
  position: relative;
  width: auto;
  font-size: 30px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 2px;
  line-height: 1;
  text-align: left;
  left: 0;
  top: 8px;
  padding: 25px;
}
.LineChartBox {
  width: 100%;
  flex: auto;
}
.contentBox {
  width: 100%;
  height: 100%;
  display: flex;
}
.LineChartItem {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column;
  padding: 0 10px;
}
.LineChartName {
  text-align: left;
  width: 100%;
  margin: auto;
  margin-left: 2%;
  line-height: 1;
}
.LineChartLimit {
  height: 100%;
}

.indicateList {
  display: flex;
  padding: 10px 25px;
}
.indicateItem {
  width: 100%;
  height: 123px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  font-size: 22px;
}
.indicateNum {
  font-size: 40px;
}
</style>



